<template>
  <div>
    <!-- 查询axios时显示的加载进行时 -->
    <van-loading style="margin-top: 100px" size="24px" vertical v-if="loading"
      >加载中...</van-loading
    >
    <!-- 刷新本页 -->
    <van-pull-refresh
      v-model="isLoading"
      success-text="刷新成功"
      @refresh="onRefresh"
      v-else
    >
      <!-- girl主要内容 -->
      <div class="girl">
        <Swiper :images="images" />

        <!-- 类型模块一 -->
        <div class="container">
          <Title
            title="青春校园"
            :loading="loading"
            :typeBooks="YouthCampusNovels"
            >青春校园</Title
          >
          <ul v-if="YouthCampusNovels">
            <BookItemTypeTwo
              :item="YouthCampusNovels[randomNumSec]"
              :key="YouthCampusNovels[randomNumSec]._id"
              :loading="loading"
            />
          </ul>
          <ul class="ul-type-one" v-if="YouthCampusNovels">
            <BookItemTypeOne
              v-for="item in YouthCampusNovels.slice(
                0 + randomNum,
                4 + randomNum
              )"
              :item="item"
              :key="item._id"
              :loading="loading"
            />
          </ul>
        </div>
        <!-- 类型模块二 -->
        <div class="container">
          <Title
            title="古代言情"
            :loading="loading"
            :typeBooks="ancientRomanceNovels"
            >古代言情</Title
          >
          <ul v-if="ancientRomanceNovels">
            <BookItemTypeTwo
              v-for="item in ancientRomanceNovels.slice(
                0 + randomNum,
                6 + randomNum
              )"
              :item="item"
              :key="item._id"
              :loading="loading"
            />
          </ul>
        </div>

        <!-- 类型模块三 -->
        <div class="container">
          <Title
            title="现代言情"
            :loading="loading"
            :typeBooks="modernRomanceNovels"
            >现代言情</Title
          >
          <van-grid column-num="3" :center="false" v-if="modernRomanceNovels">
            <BookItemTypeThree
              v-for="item in modernRomanceNovels.slice(
                0 + randomNum,
                3 + randomNum
              )"
              :item="item"
              :key="item._id"
              :loading="loading"
            />
          </van-grid>
        </div>
        <!-- 回到顶部 -->
        <BackToTop />
      </div>
    </van-pull-refresh>
  </div>
</template>
<script>
import Swiper from "@/components/Swiper.vue";
import Title from "@/components/Title.vue";
import BookItemTypeOne from "@/components/BookItemTypeOne.vue";
import BookItemTypeTwo from "@/components/BookItemTypeTwo.vue";
import BookItemTypeThree from "@/components/BookItemTypeThree.vue";
import BackToTop from "@/components/BackToTop.vue";
export default {
  components: {
    Swiper,
    Title,
    BookItemTypeOne,
    BookItemTypeTwo,
    BookItemTypeThree,
    BackToTop,
  },
  data() {
    return {
      // 小说书籍数据
      YouthCampusNovels: null,
      ancientRomanceNovels: null,
      modernRomanceNovels: null,
      randomNum: 0, //每次书籍展示的起点下标
      randomNumSec: 5, //每次书籍展示的起点下标

      maxSearch: 30, //一次最多搜索到的数量
      isLoading: false,
      loading: true, //等待loading布尔值
      loadingCount: 0,
      images: [
        {
          img:
            "https://plf-new.zhuishushenqi.com/management/images/20201218/7b215fe189e7472f9944e9f264c5f42f.jpg",
          name: "你家萌妻重生了",
          _id:'5ddb317066ecac4997e7f973',
        },
        {
          img:
            "https://plf-new.zhuishushenqi.com/management/images/20201218/c7108ad87b8e43ec8be92e278cd28db2.jpg",
            name:'十里红妆：明妧传',
            _id:'5ab3aebf6d1636e7604f0638',
        },
        {
          img:
            "https://plf-new.zhuishushenqi.com/management/images/20201127/2b8b5e6886ba472c9d6baea14de138b4.png",
            name:'他与星辰皆璀璨',
            _id:'5ee198afc045c331443b18d8',
        },
      ],
    };
  },
  watch: {
    loadingCount(n) {
      if (n >= 3) {
        this.loading = false;
      }
    },
  },
  created() {
    this.loading = true;
    this.loadingCount = 0;
    this.searchData();
  },

  mounted() {
    this.getRandomNum();
  },
  methods: {
    // 刷新当前页面的数据
    onRefresh() {
      setTimeout(() => {
        this.isLoading = false;
        this.getRandomNum();
      }, 1000);
    },
    // 获取数据
    searchData() {
      // 青春校园书籍查询
      if (
        localStorage.getItem("YouthCampusNovels") &&
        JSON.parse(localStorage.getItem("YouthCampusNovels")).expire >
          Date.now()
      ) {
        this.YouthCampusNovels = JSON.parse(
          localStorage.getItem("YouthCampusNovels")
        ).books;
        // console.log("青春校园书籍已存入本地存储"); 
        this.loadingCount++;
      } else {
        this.axios
          .get(
            "/category-info?gender=female&type=hot&major=青春校园&minor=&start=0&limit=" +
              this.maxSearch
          )
          .then((res) => {
            // console.log("青春校园书籍");
            let obj = {
              books: res.data.books.slice(0, this.maxSearch),
              expire: Date.now() + 24 * 3600 * 1000, //设置时效
            };
            this.YouthCampusNovels = obj.books;
            localStorage.setItem("YouthCampusNovels", JSON.stringify(obj));
          })
          .finally(() => {
            this.loadingCount++;
          });
      }

      // 古代言情书籍查询
      if (
        localStorage.getItem("ancientRomanceNovels") &&
        JSON.parse(localStorage.getItem("ancientRomanceNovels")).expire >
          Date.now()
      ) {
        this.ancientRomanceNovels = JSON.parse(
          localStorage.getItem("ancientRomanceNovels")
        ).books;
        // console.log("古代言情书籍已存入本地存储");
        this.loadingCount++;
      } else {
        this.axios
          .get(
            "/category-info?gender=female&type=hot&major=古代言情&minor=&start=0&limit=" +
              this.maxSearch
          )
          .then((res) => {
            // console.log("古代言情书籍");
            let obj = {
              books: res.data.books.slice(0, this.maxSearch),
              expire: Date.now() + 24 * 3600 * 1000, //设置时效
            };
            this.ancientRomanceNovels = obj.books;
            localStorage.setItem("ancientRomanceNovels", JSON.stringify(obj));
          })
          .finally(() => {
            this.loadingCount++;
          });
      }

      // 现代言情书籍查询
      if (
        localStorage.getItem("modernRomanceNovels") &&
        JSON.parse(localStorage.getItem("modernRomanceNovels")).expire >
          Date.now()
      ) {
        this.modernRomanceNovels = JSON.parse(
          localStorage.getItem("modernRomanceNovels")
        ).books;
        // console.log("现代言情书籍已存入本地存储");
        this.loadingCount++;
      } else {
        this.axios
          .get(
            "/category-info?gender=female&type=hot&major=现代言情&minor=&start=0&limit=" +
              this.maxSearch
          )
          .then((res) => {
            // console.log("现代言情书籍");
            let obj = {
              books: res.data.books.slice(0, this.maxSearch),
              expire: Date.now() + 24 * 3600 * 1000, //设置时效
            };
            this.modernRomanceNovels = obj.books;
            localStorage.setItem("modernRomanceNovels", JSON.stringify(obj));
          })
          .finally(() => {
            this.loadingCount++;
          });
      }
    },
    // 获取随机数
    getRandomNum() {
      // 获取一个随机数
      this.randomNum = Math.floor(Math.random() * (this.maxSearch - 6));
      this.randomNumSec = Math.floor(Math.random() * this.maxSearch);
    },
  },
};
</script>
<style scoped lang="less">
.girl {
  margin: 90px 0 60px 0;
  padding: 0 10px;
  box-sizing: border-box;
  transition: all 2s;
  .my-swipe .van-swipe-item {
    color: #fff;
    font-size: 20px;
    line-height: 150px;
    text-align: center;
    background-color: #39a9ed;
    img {
      height: 100%;
    }
  }
  .container {
    padding: 10px 3px;
    box-sizing: border-box;
  }
}
.ul-type-one {
  display: flex;
  margin-top: 5px;
}
</style>
